<template>
    <section class="calendar">
        <CalendarModal
            v-if="editModalOpen"
            v-on:closeModal="closeEditModal"
            v-bind:data="this.editedData"
        />
        <main class="calendar-proper" v-if="calendarData.year">
            <div
                class="calendar-day"
                v-for="day of emptyDays"
                v-bind:key="`empty-${day}`"
            />
            <div
                v-for="day of daysInMonth"
                v-bind:key="`actual-${day}`"
                :class="{
                    'calendar-day': true,
                    'calendar-day-filled': true,
                    'red': calendarData.red.indexOf(day) !== -1,
                    'blocked': day > currentDay,
                    'current': day === currentDay
                }"
                v-on:click="openEditModal(day)"
            >
                <p v-if="day > currentDay">{{day}}</p>
                <div
                    v-if="day <= currentDay"
                    class="calendar-day-footer"
                >
                    <p>
                        {{calendarData.number[day - 1]}}
                    </p>
                    <img
                        :src="calendarData.no_cube.indexOf(day) === -1 ? CubeWhite : Cube"
                    />
                </div>
            </div>
        </main>
    </section>
</template>

<script>
import CalendarModal from '@/components/Calendar/CalendarModal.vue';
import Cube from '@/assets/svg/Cube.svg';
import CubeWhite from '@/assets/svg/CubeWhite.svg';

export default {
    name: 'calendar',
    components: {
        CalendarModal,
    },
    data() {
        const currentDate = new Date();
        return {
            Cube,
            CubeWhite,
            currentDate,
            editModalOpen: false,
            editedData: {},
            currentDay: currentDate.getDate(),
            month: currentDate.getMonth(),
            year: currentDate.getFullYear(),
        };
    },
    computed: {
        calendarData() {
            return this.$store.state.calendar;
        },
        emptyDays() {
            const emptyBefore = this.currentDate.getDay() - (this.currentDay % 7) + 1;
            if (emptyBefore > 0) return emptyBefore - 1;
            return emptyBefore + 6;
        },
        daysInMonth() {
            return new Date(this.year, this.month + 1, 0).getDate();
        },
    },
    methods: {
        openEditModal(day) {
            if (day <= this.currentDay) {
                // eslint-disable-next-line
                const { _id, red, number, no_cube } = this.calendarData;
                const data = {
                    date: `${day >= 10
                        ? day : `0${day}`}.${this.month >= 9 ? this.month + 1
                        : `0${this.month + 1}`}.${this.year}`,
                    id: _id,
                    day,
                    number: number[day - 1],
                    red: red.indexOf(day) !== -1,
                    no_cube: no_cube.indexOf(day) !== -1,
                };
                this.editedData = data;
                this.editModalOpen = true;
            }
        },
        closeEditModal() {
            this.editModalOpen = false;
            this.editedData = {};
        },
    },
};
</script>

<style scoped lang="scss">
    @import '@/styles/global.scss';

    .calendar {
        &-proper {
            display: flex;
            flex-wrap: wrap;
            justify-content: center;
            max-width: 600px;
            margin: 80px auto 0;
        }

        &-day {
            position: relative;
            display: flex;
            justify-content: center;
            align-items: center;
            margin: 2px;
            width: 13%;
            height: 50px;
            font-weight: 600;

            img {
                width: 20px;
                height: 20px;
            }

            &-filled {
                background-color: $border-green;

                &:hover {
                    cursor: pointer;
                    color: $white;
                    opacity: 0.7;
                }

                &.red {
                    background-color: $red;
                }

                &.blocked {
                    cursor: default;
                    color: $black;
                    background-color: $button-shadow;
                    &:hover { opacity: 1; }
                }

                &.current {
                    border: 2px solid $black;
                    margin: 0;
                }
            }

            &-footer {
                position: absolute;
                height: 100%;
                width: 100%;

                p {
                    position: absolute;
                    top: 5px;
                    right: 5px;
                    font-size: 12px;
                }

                img {
                    position: absolute;
                    bottom: 5px;
                    left: 5px;
                }
            }
        }
    }

</style>
